.contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center ;
    gap: 80px;
    margin: 80px 170px;
    
}
.contact-title{
position: relative;
}

.contact-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.contact-title img{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}
.contact-section{
    display: flex;
    gap: 150px;
}
.contact-left{
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.contact-left h1{
    font-size: 80px;
    font-weight: 700;
    background:linear-gradient(270deg,#DF8908 80.09%,#B415FF 100.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent ;
}

.contact-left p {
    max-width: 550px;
    color: #D8D8D8;
    font-size: 20px;
    line-height: 35px;
}
.contact-details{
    display: flex;
    flex-direction: column;
    gap: 30px;
    color: #D8D8D8;
    font-size: 22px;
}
.contact-detail{
    display: flex;
    align-items: center;
    gap: 20px;
}
.contact-right{
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 30px;

}
.contact-right label{
    color: #D8D8D8;
    font-size: 22px;
    font-weight: 500;
}
.contact-right input{
    border: none;
    /* width: 700px; */
    height: 40px;
    padding-left: 20px;
    border-radius: 4px;
    background: #32323c;
    color: #A0A0A0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
}
.contact-right textarea{
    border: none;
    /* width: 500px; */
    padding: 25px;
    border-radius: 4px;
    background: #32323c;
    color: #A0A0A0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
}
.contact-submit{
    border: none;
    padding: 20px 60px;
    border-radius: 50px;
    color: white;
    background:linear-gradient(270deg,#DF8908 10%,#B415FF 100%);
    font-size: 22px;
    margin-bottom: 50px;
    cursor: pointer;
    transition: 0.3s;
}
.contact-submit:hover{
    transform: scale(1.1);
    transition: 0.3s;
}

@media (max-width: 768px) {
    .contact {
        gap: 40px;
        margin: 50px 6%;
        align-items: start;
    }
    .contact-title h1 {
        padding: 0;
        font-size: 40px;
    }
    .contact-title img {
        width: 100px;
        right: -40px;
    }
    .contact-section {
        flex-direction: column;
    }
    .contact-left {
        margin-bottom: 30px;
    }
    .contact-left h1 {
        font-size: 35px;
    }
    .contact-detail {
        gap: 12px;
    }
    .contact-detail img {
        width: 20px;
    }
    .contact-right label {
        font-size: 14px;
    }

}