.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center ;
    gap: 80px;
    margin: 80px 170px;
    
}
.services-title{
position: relative;
}

.services-title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}
.services-title img{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}
.services-format{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    padding: 60px;
    border-radius: 10px;
    border: 2px solid white;
    transition: 0.4s;
    cursor: pointer;
    height: 320px;
}
.services-container{
    display: grid ;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
    margin-bottom: 80px;
}
.services-format h3{
    font-size: 24px;
    font-weight: 600;
}
.services-format h2{
    font-size: 30px;
    font-weight: 800;
    background:linear-gradient(270deg,#DF8908 50.09%,#B415FF 90.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent ;
}
.services-format p{
    color: #d4d4d4;
    font-size: 24px;
    line-height: 40px;
    max-width:300px ;
}

.services-readmore{
    display: flex;
    gap: 20px;
    align-items: center;
    margin-top: 20px;
}
.services-format:hover{
    border: 2px solid #ff00ff;
    background-image: linear-gradient(45deg,#3f0028,#582300);
    transform: scale(1.05);
    transition: 0.4s;
}

@media (max-width: 768px) {
    .services {
        align-items: start;
        gap: 70px;
        margin: 20px 6%;
    }
    .services-title h1 {
        padding: 0;
        font-size: 40px;
    }
    .services-title img {
        width: 100px;
        right: -40px;
    }
    .services-container {
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
    .services-format {
        gap: 10px;
        padding: 20px;
    }
    .services-format h2 {
        font-size: 20px;
    }
    .services-format p {
        font-size: 16px;
        line-height: 26px;
        max-width: 300px;
    }
    .services-format p {
        font-size: 16px;
        line-height: 26px;
        max-width: 300px;
    }
    
}